<template>
  <!-- this.myPosition -->
  <!-- 目前使用袁老师的AK -->
  <baidu-map
    @ready="handler"
    class="map"
    :zoom="zoom"
    :min-zoom="13"
    :center="{ lng: 120.216974, lat: 30.248059 }"
    ref="baiduMap"
    ak="TpyIBVQAG5YyXj3IdbUVAM4ttar4sURe"
  >
    <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
    <template v-for="(item, index) in signData">
      <bm-label
        content="签到点"
        :key="String(index) + '-label'"
        :position="item.point"
        :labelStyle="labelStyle"
        title="签到点"
      />
      <!-- 绘制签到范围 -->
      <bm-circle
        :key="String(index) + '-circle'"
        :center="item.point"
        :radius="item.radius"
        stroke-color="blue"
        :stroke-opacity="0.33"
        :stroke-weight="1"
      ></bm-circle>
      <!-- 绘制锚点 -->
      <bm-marker
        :key="String(index) + '-marker'"
        :position="item.point"
        :dragging="false"
        animation="BMAP_ANIMATION_BOUNCE"
        @click="changeCenter(index, item)"
      >
        <bm-info-window
          :show="currentIndex === index"
          @close="infoWindowClose"
          :width="250"
          :title="item.name"
          :key="index"
          class="bmwindow"
        >
          <div class="direct">
            <input
              type="button"
              class="btn"
              value="到这里去"
              @click="gotoMap(item.point)"
            />
          </div>
        </bm-info-window>
      </bm-marker>
    </template>
  </baidu-map>
</template>


<script type="text/javascript" src="http://api.map.baidu.com/api?v=2.0&ak=TpyIBVQAG5YyXj3IdbUVAM4ttar4sURe"></script>
<script type="text/javascript" src="http://developer.baidu.com/map/jsdemo/demo/convertor.js"></script>

<script>
import { mapState, mapMutations } from "vuex";
import { SET_CITY, SET_POSITION } from "@/store/mutation_types";

import BaiduMap from "vue-baidu-map/components/map/Map.vue";
import BmNavigation from "vue-baidu-map/components/controls/Navigation";
import BmGeolocation from "vue-baidu-map/components/controls/Geolocation";
import BmMarker from "vue-baidu-map/components/overlays/Marker";
import BmCircle from "vue-baidu-map/components/overlays/Circle";
import BmInfoWindow from "vue-baidu-map/components/overlays/InfoWindow";
import BmLabel from "vue-baidu-map/components/overlays/Label";
import Branch from "./Branch";
import BScroll from "better-scroll";

export default {
  name: "branch-map",
  components: {
    BaiduMap,
    BmNavigation,
    BmGeolocation,
    BmMarker,
    BmInfoWindow,
    Branch,
    BmCircle,
    BmLabel,
  },
  props: {
    branchesList: {
      type: Array,
      default() {
        return [];
      },
    },
    zoom: {
      type: Number,
      default: 15,
    },
    parentIndex: {
      type: Number,
      default: -1,
    },
    signData: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  watch: {},
  created() {},
  data() {
    return {
      map: {},
      myUrl: require("@/assets/myicon.png"),
      BMap: {},
      curCity: "",
      startMovePosition: {
        lat: 0,
        lng: 0,
      },
      currentIndex: -1, // 默认值为-1 默认不显示
      labelStyle: {
        padding: 0,
        margin: 0,
        display: "inline-block",
        "line-height": 1.5,
        background: "#0066B3",// 建行蓝 RGB
        color: "#fff",
        "font-weight": 700,
        "text-align": "center",
        padding: "6px",
        border: "0px solid #e5e5e5",
        "box-shadow": "0 0 10px 3px rgba(0, 0, 0, 0.1)",
        "border-radius": "6px",
      },
    };
  },
  computed: {
    ...mapState(["myPosition", "myCity"]),
  },
  methods: {
    ...mapMutations({
      setCity: SET_CITY,
      setPosition: SET_POSITION,
    }),
    savePosition(lng, lat) {
      this.setPosition({ lng, lat });
      this.startMovePosition = { lng, lat };
    },
    handler({ BMap, map }) {
      this.setPosition({});
      // 强制获取一次地理位置
      useNativeGetCurrentPosition()
        .then(() => {
          // 定位
          this.map = map;
          this.BMap = BMap;
          // 获取定位
          let geolocation = new BMap.Geolocation();
          const _this = this;
          geolocation.getCurrentPosition(function (r) {
            if (this.getStatus() === BMAP_STATUS_SUCCESS) {
              // 保存当前定位
              _this.savePosition(r.point.lng, r.point.lat);
              // alert(`${r.point.lng},${r.point.lat}`);
              let myIcon = new BMap.Icon(_this.myUrl, new BMap.Size(32, 32));
              // 绘制当前位置图标
              let mk = new BMap.Marker(r.point);
              mk.setIcon(myIcon);
              map.addOverlay(mk);
              // 移动到定位点
              map.panTo(r.point);
              console.log(r.point);
              mk.setAnimation(BMAP_ANIMATION_BOUNCE);
              // 修改父组件的变量
              _this.$parent.mapState = true; // 显示地图组件
              _this.$parent.mapLoading = false; // 关闭刷新动画
            } else {
              alert(
                `获取当前位置信息失败：${this.getStatus()}\n请重新进入本页面`
              );
              console.log("failed ", this.getStatus());
            }
          });
        })
        .catch(() => {
          alert("获取定位失败,请尝试退出后重新进入");
        });
    },
    async reloadMap() {
      // 快速刷新Map组件
      const result = this.simpleTimeout();
      result.then(() => {
        this.$parent.mapState = true;
      });
    },
    // 定义一个简单的阻塞定时器
    simpleTimeout() {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          this.$parent.mapState = false;
          this.$parent.mapLoading = true;
          resolve();
        }, 5);
      });
    },
    changeCenter(index, item) {
      console.log("==changecenter=====" + index, item);
      this.infoWindowOpen(index, item);
      this.map.panTo(new BMap.Point(item.point.lng, item.point.lat));
    },
    infoWindowOpen(index, item) {
      // 直接将currentIndex置为-1，默认先关闭所有的弹窗
      this.currentIndex = -1;
      setTimeout(() => {
        // 设置显示的index
        // 如果重复，将会从父组件传来-1，也就不显示了
        this.currentIndex = index;
      }, 100);
    },
    infoWindowClose(e) {
      //infowwindowclose在open之前调用，所以当有一个窗口在显示时，如果点击窗口，则窗口被此事件关闭了
      //     this.currentIndex = -1
      console.log(e);
      console.log("=======info window is closed");
    },
    gotoMap(destination) {
      console.log(destination);
      //经纬度如果不准的话将导航不出来，此时可以通过中文导航，如果通过中文，还有一个region一定要输入，否则不知道具体地方
      //可先通过经纬度试一下，如果不行再换名称
      let href =
        "http://api.map.baidu.com/direction?origin=latlng:" +
        this.myPosition.lat +
        "," +
        this.myPosition.lng +
        "|name:我的位置" +
        "&destination=" +
        destination.lat +
        "," +
        destination.lng +
        "&mode=driving&region=" +
        "杭州" +
        "&&output=html&src=tlzxzj.com";
      console.log("=====" + href);
      // alert(href)
      window.location.href = href;
      //window.location.href = 'http://api.map.baidu.com/direction?origin=latlng:29.88525897,121.57900597|name:我的位置&destination=建行中山支行&mode=driving&region=杭州&output=html&src=yun.tlzxzj.com'
    },
  },
};

function useNativeGetCurrentPosition() {
  return new Promise((resolve, reject) => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          console.log(position);
          resolve();
        },
        (msg) => {
          console.log("定位失败的错误提醒", msg);
          reject();
        },
        // FIXME 电脑端设置下方参数可能导致无法获取定位，手机端无问题
        {
          // 指示浏览器获取高精度的位置，默认为false
          enableHighAcuracy: true,
          // 指定获取地理位置的超时时间，默认不限时，单位为毫秒
          // timeout: 36000,
          // 最长有效期，在重复获取地理位置时，此参数指定多久再次获取位置。
          maximumAge: 0,
        }
      ); // 成功方法，失败方法
    } else {
      console.log("在目前环境中不支持地理定位。请稍后重试");
    }
  });
}
</script>

<style scoped>
.map {
  width: 100%;
  height: 100%;
}
.direct {
  text-align: center;
}
.wrapper {
  height: 168px;
  overflow: auto;
}
.btn {
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #333;
  background-color: #ebebeb;
  border-color: #adadad;
  padding: 5px 10px;
  font-size: 12px;
  line-height: 1.5;
  border-radius: 3px;
}
</style>
